import {useState} from "react";
const CityWeatherReporter=()=>{const a1="https://api.openweathermap.org/data/2.5/weather?q=";const a3="&appid=d6734eae54491062ff3bd0a7797a37b9"; const [a2,A2]=useState({city:"",message:"",h4:null}); return(<div><form onSubmit={f=>{f.preventDefault(); fetch(a1+a2.city+a3).then(d=>d.json()).then(d=>A2({...a2,h4:{description:d.weather[0].description,temp:d.main.temp,pressure:d.main.pressure,humidity:d.main.humidity,country:d.sys.country,city:d.name}})).catch(d=>alert(d));}}><input type="submit" value="Get Weather info" /><input type="text" placeholder="Enter a City Name " name="city" value={a2.city} onChange={c=>A2({...a2,city:c.target.value})}/></form><h4>{a2.h4?<ul style={{listStyleType:"none"}}><li>Description : {a2.h4.description}</li><li>Temprature number : {a2.h4.temp}</li><li>Pressure number : {a2.h4.pressure}</li><li>Humidity number : {a2.h4.humidity}</li><li>Showing results for {a2.h4.city}, country code is {a2.h4.country}</li></ul>:"Hi There..."}</h4></div>);};

export default CityWeatherReporter;